import { BaseValueObject } from '../Base';

export const AUTHENTICATION_PROVIDER_LIST = ['GOOGLE'] as const;
export type AUTHENTICATION_PROVIDER_TYPE =
  (typeof AUTHENTICATION_PROVIDER_LIST)[number];

export class AuthenticationProvider extends BaseValueObject<
  'AuthenticationProvider',
  string
> {
  static from(value: string): AuthenticationProvider {
    return new AuthenticationProvider(value);
  }

  protected validate(value: string): string {
    if (
      !AUTHENTICATION_PROVIDER_LIST.includes(
        value as AUTHENTICATION_PROVIDER_TYPE,
      )
    ) {
      throw new Error('Invalid authentication provider');
    }
    return value;
  }
}
